import './App.css';
import axios from 'axios';
import { useEffect, useState } from 'react';

// const BASE_URL = 'http://localhost:8000/'
const BASE_URL = 'https://csgo-site.herokuapp.com/'

function App() { 
  const [response, setResponse] = useState({});
  const [isLogin, setLogin] = useState(false);
  function login() {
    window.location.href = `${BASE_URL}accounts/steam/login/`
  }

  function logout() {
    axios.get(`${BASE_URL}accounts/logout/`, {withCredentials: true}).catch(() => {
      setLogin(false);
      setResponse({});
    })
    }

  function getCookie(name) {
  let matches = document.cookie.match(new RegExp(
    "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
  ));
  return matches ? decodeURIComponent(matches[1]) : undefined;
}

  useEffect(() =>{
    getInfo();
  },[])

    function getInfo() { 
    axios.get(`${BASE_URL}api/me/`, {withCredentials: true}).then((resp) => {
      setResponse(resp.data);
      setLogin(true);
    }).catch(() => {
      setLogin(false)})
   }

  return (
    <div className="App">
    <h6 className="header">ЖМИТЕ НЕ СКАМ!!!</h6>
      {!isLogin && <img src="https://community.cloudflare.steamstatic.com/public/images/signinthroughsteam/sits_02.png" onClick={login}className="login-btn"/>}
      <img src={response?.extra_data?.avatarfull || 'https://r3.mt.ru/r2/photoDBD0/20279248353-0/jpeg/bp.jpeg'} />
      {isLogin && <button onClick={logout}>Logout</button>}
    </div>
  );
}

export default App;
